"use client";
import Navbar from "@/app/components/Navbar";
import { useState, useEffect } from "react";
import { useBudget } from "@/hooks/useBudget";

export default function BudgetPage() {
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [selectedBudget, setSelectedBudget] = useState(null);
  const [mounted, setMounted] = useState(false);
  const [formData, setFormData] = useState({
    name: "",
    amount: "",
    period: "month",
    icon: "shopping_cart",
  });

  // Get current user ID (you might want to get this from your auth context)
  const userId = "current-user-id"; // Replace with actual user ID from your auth system

  const {
    getBudgets,
    createBudget,
    updateBudget,
    deleteBudget,
    mutations: { createBudget: createMutation, updateBudget: updateMutation },
  } = useBudget();

  const { data: budgetsData, isLoading, error } = getBudgets(userId);
  const budgets = budgetsData?.data || [];

  useEffect(() => {
    setMounted(true);
  }, []);

  const openModal = (budget = null) => {
    if (budget) {
      setSelectedBudget(budget);
      setFormData({
        name: budget.name,
        amount: budget.amount.replace(/[^0-9.]/g, ""), // Remove PHP and commas
        period: budget.period.replace("/", ""),
        icon: budget.icon,
      });
    } else {
      setSelectedBudget(null);
      setFormData({
        name: "",
        amount: "",
        period: "month",
        icon: "shopping_cart",
      });
    }
    setIsModalOpen(true);
  };

  const closeModal = () => {
    setIsModalOpen(false);
    setSelectedBudget(null);
    // Reset mutations when closing modal
    createMutation.reset();
    updateMutation.reset();
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    const budgetData = {
      userId,
      name: formData.name,
      amount: parseFloat(formData.amount),
      period: `/${formData.period}`,
      icon: formData.icon,
    };

    try {
      if (selectedBudget) {
        await updateBudget(selectedBudget.id, budgetData);
      } else {
        await createBudget(budgetData);
      }
      closeModal();
    } catch (error) {
      console.error("Error saving budget:", error);
    }
  };

  const handleDelete = async (budgetId) => {
    if (confirm("Are you sure you want to delete this budget?")) {
      try {
        await deleteBudget(budgetId);
      } catch (error) {
        console.error("Error deleting budget:", error);
      }
    }
  };

  const iconOptions = [
    "shopping_cart",
    "lightbulb",
    "train",
    "movie",
    "home",
    "savings",
    "restaurant",
    "local_gas_station",
    "fitness_center",
    "school",
    "local_hospital",
    "flight",
    "shopping_bag",
    "wifi",
    "water_drop",
  ];

  if (isLoading) {
    return (
      <div className="relative flex h-auto min-h-screen w-full flex-col bg-[#21121e] overflow-x-hidden">
        <Navbar />
        <div className="flex-1 flex items-center justify-center">
          <div className="text-white text-lg">Loading budgets...</div>
        </div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="relative flex h-auto min-h-screen w-full flex-col bg-[#21121e] overflow-x-hidden">
        <Navbar />
        <div className="flex-1 flex items-center justify-center">
          <div className="text-red-400 text-lg">Error loading budgets</div>
        </div>
      </div>
    );
  }

  return (
    <div className="relative flex h-auto min-h-screen w-full flex-col bg-[#21121e] overflow-x-hidden">
      <div className="layout-container flex h-full grow flex-col">
        <Navbar />

        {/* Main Content */}
        <div className="px-4 sm:px-8 md:px-12 lg:px-20 xl:px-40 flex flex-1 justify-center py-5">
          <div className="layout-content-container flex flex-col w-full flex-1">
            <main className="flex-1 mt-6 md:mt-10">
              {/* Page Header with Animation */}
              <div
                className={`flex flex-wrap justify-between gap-4 p-4 items-center transition-all duration-700 ${
                  mounted
                    ? "opacity-100 translate-y-0"
                    : "opacity-0 translate-y-4"
                }`}
              >
                <div className="flex min-w-72 flex-col gap-2">
                  <h1 className="text-white text-3xl md:text-4xl font-black leading-tight tracking-[-0.033em]">
                    My Budgets
                  </h1>
                  <p className="text-[#c695bb] text-base font-normal leading-normal">
                    Here are your allocated budgets for the current period.
                  </p>
                </div>

                {/* Add New Budget Button with Enhanced Hover */}
                <button
                  onClick={() => openModal()}
                  className="group relative flex min-w-[84px] max-w-[480px] cursor-pointer items-center justify-center overflow-hidden rounded-lg h-10 px-5 bg-[#9c167f] text-white text-sm font-bold leading-normal tracking-[0.015em] transition-all duration-500 hover:bg-[#b51a97] hover:shadow-2xl hover:shadow-[#9c167f]/50 hover:-translate-y-1 active:translate-y-0"
                >
                  <span className="truncate relative z-10">Add New Budget</span>
                  <div className="absolute inset-0 bg-gradient-to-r from-transparent via-white/20 to-transparent -translate-x-full group-hover:translate-x-full transition-transform duration-1000"></div>
                </button>
              </div>

              {/* Budget List with Staggered Animations */}
              <div className="mt-6 md:mt-8 flex flex-col gap-3 px-4">
                {budgets.length === 0 ? (
                  <div className="text-center py-12">
                    <div className="text-[#c695bb] text-lg mb-4">
                      No budgets found
                    </div>
                    <button
                      onClick={() => openModal()}
                      className="px-6 py-2 bg-[#9c167f] text-white rounded-lg hover:bg-[#b51a97] transition-colors"
                    >
                      Create Your First Budget
                    </button>
                  </div>
                ) : (
                  budgets.map((budget, index) => (
                    <div
                      key={budget.id}
                      className={`group flex flex-col sm:flex-row items-center gap-4 bg-[#321b2d] p-4 min-h-[72px] justify-between rounded-xl border border-[#63365a] transition-all duration-500 hover:border-[#9c167f] hover:shadow-xl hover:shadow-[#9c167f]/25 hover:-translate-y-1 ${
                        mounted
                          ? "opacity-100 translate-y-0"
                          : "opacity-0 translate-y-4"
                      }`}
                      style={{
                        transitionDelay: `${index * 100}ms`,
                      }}
                    >
                      <div className="flex items-center gap-4 w-full">
                        <div className="text-white flex items-center justify-center rounded-lg bg-[#63365a] shrink-0 size-12 transition-all duration-300 group-hover:bg-[#9c167f] group-hover:scale-110 group-hover:rotate-3">
                          <span className="material-symbols-outlined text-2xl">
                            {budget.icon}
                          </span>
                        </div>
                        <div className="flex flex-col justify-center flex-grow">
                          <p className="text-white text-base font-medium leading-normal line-clamp-1">
                            {budget.name}
                          </p>
                          <p className="text-[#c695bb] text-sm font-normal leading-normal line-clamp-2">
                            Allocated Budget
                          </p>
                        </div>
                        <div className="shrink-0 text-right">
                          <p className="text-white text-lg font-bold leading-normal">
                            PHP{" "}
                            {parseFloat(budget.amount).toLocaleString("en-US", {
                              minimumFractionDigits: 2,
                              maximumFractionDigits: 2,
                            })}
                            <span className="text-sm font-normal text-[#c695bb]">
                              {budget.period}
                            </span>
                          </p>
                        </div>
                      </div>
                      <div className="flex gap-2 sm:ml-4 w-full sm:w-auto">
                        <button
                          onClick={() => openModal(budget)}
                          className="flex w-full sm:w-auto min-w-[84px] cursor-pointer items-center justify-center overflow-hidden rounded-lg h-10 px-4 bg-[#321b2d] text-white gap-2 text-sm font-bold leading-normal tracking-[0.015em] transition-all duration-300 hover:bg-[#3d2245] border border-[#63365a] hover:border-[#9c167f] hover:scale-105"
                        >
                          <span className="material-symbols-outlined text-lg">
                            edit
                          </span>
                          <span className="truncate">Edit</span>
                        </button>
                        <button
                          onClick={() => handleDelete(budget.id)}
                          className="flex w-full sm:w-auto min-w-[84px] cursor-pointer items-center justify-center overflow-hidden rounded-lg h-10 px-4 bg-[#321b2d] text-red-400 gap-2 text-sm font-bold leading-normal tracking-[0.015em] transition-all duration-300 hover:bg-[#3d2245] border border-[#63365a] hover:border-red-500 hover:scale-105"
                        >
                          <span className="material-symbols-outlined text-lg">
                            delete
                          </span>
                          <span className="truncate">Delete</span>
                        </button>
                      </div>
                    </div>
                  ))
                )}
              </div>
            </main>
          </div>
        </div>
      </div>

      {/* Budget Modal */}
      {isModalOpen && (
        <div className="fixed inset-0 z-50 flex items-center justify-center bg-black/60 backdrop-blur-sm">
          <div
            className={`bg-[#321b2d] border border-[#63365a] rounded-2xl p-6 mx-4 w-full max-w-md transform transition-all duration-500 ${
              isModalOpen ? "scale-100 opacity-100" : "scale-95 opacity-0"
            }`}
          >
            <div className="flex items-center justify-between mb-6">
              <h2 className="text-white text-xl font-bold">
                {selectedBudget ? "Edit Budget" : "Add New Budget"}
              </h2>
              <button
                onClick={closeModal}
                className="text-[#c695bb] hover:text-white transition-colors duration-200 p-1 rounded-lg hover:bg-white/10"
              >
                <span className="material-symbols-outlined text-2xl">
                  close
                </span>
              </button>
            </div>

            <form onSubmit={handleSubmit} className="space-y-4">
              <div>
                <label className="block text-[#c695bb] text-sm font-medium mb-2">
                  Budget Name
                </label>
                <input
                  type="text"
                  value={formData.name}
                  onChange={(e) =>
                    setFormData({ ...formData, name: e.target.value })
                  }
                  className="w-full bg-[#21121e] border border-[#63365a] rounded-lg px-3 py-2 text-white focus:outline-none focus:border-[#9c167f] transition-colors duration-300"
                  placeholder="Enter budget name"
                  required
                />
              </div>

              <div>
                <label className="block text-[#c695bb] text-sm font-medium mb-2">
                  Amount
                </label>
                <input
                  type="number"
                  value={formData.amount}
                  onChange={(e) =>
                    setFormData({ ...formData, amount: e.target.value })
                  }
                  className="w-full bg-[#21121e] border border-[#63365a] rounded-lg px-3 py-2 text-white focus:outline-none focus:border-[#9c167f] transition-colors duration-300"
                  placeholder="0.00"
                  step="0.01"
                  min="0"
                  required
                />
              </div>

              <div>
                <label className="block text-[#c695bb] text-sm font-medium mb-2">
                  Period
                </label>
                <select
                  value={formData.period}
                  onChange={(e) =>
                    setFormData({ ...formData, period: e.target.value })
                  }
                  className="w-full bg-[#21121e] border border-[#63365a] rounded-lg px-3 py-2 text-white focus:outline-none focus:border-[#9c167f] transition-colors duration-300"
                >
                  <option value="month">Monthly</option>
                  <option value="week">Weekly</option>
                  <option value="year">Yearly</option>
                </select>
              </div>

              <div>
                <label className="block text-[#c695bb] text-sm font-medium mb-2">
                  Icon
                </label>
                <div className="grid grid-cols-5 gap-2">
                  {iconOptions.map((icon) => (
                    <button
                      key={icon}
                      type="button"
                      onClick={() => setFormData({ ...formData, icon })}
                      className={`p-2 rounded-lg border transition-all duration-300 ${
                        formData.icon === icon
                          ? "bg-[#9c167f] border-[#9c167f] scale-110"
                          : "bg-[#21121e] border-[#63365a] hover:border-[#9c167f] hover:scale-105"
                      }`}
                    >
                      <span className="material-symbols-outlined text-white text-lg">
                        {icon}
                      </span>
                    </button>
                  ))}
                </div>
              </div>

              {/* Error Messages */}
              {(createMutation.isError || updateMutation.isError) && (
                <div className="text-red-400 text-sm bg-red-400/10 border border-red-400/20 rounded-lg p-3">
                  Error saving budget. Please try again.
                </div>
              )}

              <div className="flex gap-3 pt-4">
                <button
                  type="button"
                  onClick={closeModal}
                  className="flex-1 py-3 px-4 bg-transparent border border-[#63365a] text-[#c695bb] rounded-lg hover:bg-[#3d2245] hover:text-white transition-all duration-300 hover:scale-105"
                >
                  Cancel
                </button>
                <button
                  type="submit"
                  disabled={
                    createMutation.isPending || updateMutation.isPending
                  }
                  className="flex-1 py-3 px-4 bg-[#9c167f] text-white rounded-lg hover:bg-[#b51a97] transition-all duration-300 hover:scale-105 disabled:opacity-50 disabled:cursor-not-allowed flex items-center justify-center gap-2"
                >
                  {createMutation.isPending || updateMutation.isPending ? (
                    <>
                      <div className="w-4 h-4 border-2 border-white border-t-transparent rounded-full animate-spin"></div>
                      Saving...
                    </>
                  ) : selectedBudget ? (
                    "Update Budget"
                  ) : (
                    "Create Budget"
                  )}
                </button>
              </div>
            </form>
          </div>
        </div>
      )}

      {/* Material Icons CSS */}
      <link
        href="https://fonts.googleapis.com/css2?family=Material+Symbols+Outlined"
        rel="stylesheet"
      />

      {/* Custom Styles for Material Icons */}
      <style jsx>{`
        .material-symbols-outlined {
          font-variation-settings: "FILL" 0, "wght" 400, "GRAD" 0, "opsz" 24;
        }
      `}</style>
    </div>
  );
}
